import React,{useState} from 'react';
import {HashRouter as Router,Route,Switch,useRouteMatch} from 'react-router-dom';
import DashBoard from './DashBoard';
import CashIn from './CashIn';
import CashOut from './CashOut';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import * as GiIcons from "react-icons/gi";
import { Link } from 'react-router-dom';
import '../css/homepage.scss';
import { IconContext } from 'react-icons';

/*
  Homepage is a funtion component to show the cashin, cashout and dashboard page
*/
function HomePage(props){
  let match = useRouteMatch();
  const [sidebar, setSidebar] = useState(false);
        return(
            <div>
        <Router>
        <IconContext.Provider value={{ color: '#fff' }}>
        <div className='navbar'>
          <button className="logoutbutton" onClick={()=>{props.history.push("/")}}>Logout</button>
          <h1 className="welcomeuser">Welcome Manikandan</h1>
          <Link to='#' className='menu-bars'>
            <FaIcons.FaBars onClick={()=>setSidebar(!sidebar)} />
          </Link>
        </div>
        <nav className={sidebar ? 'nav-menu active' : 'nav-menu'}>
          <ul className='nav-menu-items' onClick={()=>setSidebar(!sidebar)}>
            <li className='navbar-toggle'>
              <Link to='#' className='menu-bars'>
                <AiIcons.AiOutlineClose />
              </Link>
            </li>
                <li key={0} className='nav-text'>
                  <Link to="/">
                  <AiIcons.AiFillHome />
                    <span>DashBoard</span>
                  </Link>
                </li>
                <li key={1} className='nav-text'>
                  <Link to="/cashin">
                  <GiIcons.GiPayMoney />
                    <span>CashIn</span>
                  </Link>
                </li>
                <li key={2} className='nav-text'>
                  <Link to="/cashout">
                  <GiIcons.GiReceiveMoney />,
                    <span>Cashout</span>
                  </Link>
                </li>
          </ul>
        </nav>
      </IconContext.Provider>
        <Switch>
          <Route path="/" exact component={DashBoard} />
          <Route path="/cashin" component={CashIn} />
          <Route path="/cashout" component={CashOut} />
        </Switch>
      </Router>
            </div>
        )
    }

/*
  Exporting the homepage component such that any component can use this component by importing this component
*/
export default HomePage;